.projectsBlock {
    border: 1px solid;


}
.projectsContainer {
    border: 1px solid;
    flex-direction: column;
}
.projects{
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

@media screen and (max-width: 1014px) {
    .projects {
        align-items: center;
        flex-direction: column;
    }

}
.title{
    color: #666;
    font-size: 56px;
    font-weight: 900!important;
    text-transform: uppercase;
    margin: 0;
}
.title span{
    color: #72b626;
}
